@font-face {
  src: local(HealB), url("HealTheWebB-Regular.b4c2386d.otf");
  font-family: HealB;
  src: local(HealA), url("HealTheWebA-Regular.74330abb.otf");
  font-family: HealA;
}

:root {
  font-family: HealA;
  font-size: 62.5%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: HealA;
}

#bkvid {
  width: 100vw;
  min-height: 100vh;
  object-fit: fill;
  z-index: -1;
  position: absolute;
  top: 0;
  bottom: 0;
}

header {
  height: 25vh;
  border-bottom: 2px solid #000;
  border-radius: 41% 59% 51% 49% / 50% 47% 53% 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu {
  margin-bottom: 2vh;
}

.dropdwn_btn {
  color: #000;
  cursor: help;
  width: 5vw;
  background: none;
  border: none;
  font-size: 4rem;
  transition: all 4s;
  animation: eye 13s infinite;
}

@keyframes eye {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotateY(360deg);
  }

  100% {
    transform: rotatY(10deg);
  }
}

.dropdown_cont {
  color: #000;
  min-height: 8.6rem;
  display: none;
  position: relative;
  left: 1rem;
}

.dropdown_cont a {
  float: none;
  color: #000;
  text-align: left;
  padding: .2rem .6rem;
  text-decoration: none;
  display: block;
}

.dropdown_cont a:hover {
  color: #fff;
  background-color: #000;
  border-radius: 68px 32px 62px 68px / 21px 67px 33px 79px;
}

.dropdown:hover .dropdown_cont {
  width: 10rem;
  display: block;
}

.dropdown:hover .dropdwn_btn {
  display: none;
}

.info {
  text-align: center;
  width: 100%;
  height: 100%;
  margin-bottom: 5vh;
  padding: .5rem;
}

.info p {
  margin-bottom: 1rem;
}

.talk {
  text-align: center;
  width: 100vw;
  height: 10vh;
  padding: 1rem;
}

.talk a {
  color: #000;
  background-color: #fff;
  border-radius: 23% 77% 0% 100%;
  padding: 1rem;
  font-size: 2rem;
  text-decoration: none;
}

.talk a:hover {
  color: #fff;
  background-color: #000;
  border-radius: 23% 77% 0% 100% / 83% 36% 64% 17%;
}

@media screen and (max-width: 280px) {
  .talk a {
    font-size: 1rem;
  }
}

@media screen and (min-width: 600px) {
  .info p {
    font-size: 2rem;
  }

  .talk {
    margin-top: 6rem;
  }
}

